import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Lightbox from 'react-image-lightbox';

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: 500,
    height: 450,
  },
});

class ImageDisplay extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      photoIndex: 0,
      isOpen: false,
      imageList: props.imageList,
      filterImageList: props.imageList,
    }
  }

  componentDidUpdate(prevProps) {
    const { filterImageGroup } = this.props;
    if (filterImageGroup !== prevProps.filterImageGroup) {
      this.handleFilterImage();
    }
  }

  onImageOpen(index){
    this.setState({
      isOpen: true,
      photoIndex: index
    })
  }

  async handleFilterImage() {
    const { imageList } = this.state;
    const { filterImageGroup } = this.props;
    if (filterImageGroup === 'all') {
      this.setState({ filterImageList: imageList });
    } else {
      const filterImageList = await imageList.filter(image => image.category.toLowerCase().includes(filterImageGroup));
      this.setState({ filterImageList: filterImageList });
    }
  }

  render() {
    const { classes } = this.props;
    const { filterImageList, photoIndex, isOpen, } = this.state;
    console.log(filterImageList);
    const className = "col-md-3 col-12 join fedIn";
    return (
      <div className={classes.root} style={{ marginTop: '10px' }}>
        {filterImageList.map((image, index) => (
          <div className={className} style={{ padding: '0 0' }} onClick={()=>this.onImageOpen(index)}>
            <img src={image.img} className="img-fluid" style={{ borderRadius: '4px' }} />
          </div>
        ))}

        {
          isOpen && (
            <Lightbox
              mainSrc={filterImageList[photoIndex].img}
              nextSrc={filterImageList[(photoIndex + 1) % filterImageList.length].img}
              prevSrc={filterImageList[(photoIndex + filterImageList.length - 1) % filterImageList.length].img}
              onCloseRequest={() => this.setState({ isOpen: false })}
              onMovePrevRequest={() =>
                this.setState({
                  photoIndex: (photoIndex + filterImageList.length - 1) % filterImageList.length,
                })
              }
              onMoveNextRequest={() =>
                this.setState({
                  photoIndex: (photoIndex + 1) % filterImageList.length,
                })
              } />
          )
        }
      </div>
    );
  }
}

ImageDisplay.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(ImageDisplay);