import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import { imageList } from './image-list';
import { NavLink } from 'react-router-dom';
import ImageDisplay from './images-display';

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  margin: {
    margin: theme.spacing.unit,
  },
  cssRoot: {
    color: 'black',
    padding: '0 30px',
    backgroundColor: 'transparent',
    border: '2px solid',
    borderColor: 'transparent',
    '&:hover': {
      backgroundColor: 'transparent',
      color: '#4d90fe',
      borderColor: '#4d90fe',
      outline: '0'
    },
    '&:active': {
      backgroundColor: 'transparent',
      color: '#4d90fe',
      borderColor: '#4d90fe',
    },
    '&:focus': {
      backgroundColor: 'transparent',
      color: '#4d90fe',
      borderColor: '#4d90fe',
      outline: '0'
    },
  },
});

class ImageGallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      filterImageGroup: 'all'
    }
    this.handeleFilterItem = this.handeleFilterItem.bind(this);
    this.handleDrawerClose = this.handleDrawerClose.bind(this);
    this.handleDrawerOpen = this.handleDrawerOpen.bind(this);
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  handeleFilterItem(name) {
    this.setState({ filterImageGroup: name })
  }

  render() {
    const { classes } = this.props;
    const { filterImageGroup } = this.state;
    let toggle;
    if (window.innerWidth < 992) {
      toggle = {
        "data-toggle": "collapse",
        "data-target": "#menu"
      }
    } else {
      toggle = "";
    }
    return (
      <div>
        <div className="navbar navbar-expand-lg custome-toggler navbar-light navigation-wrapper master-padding" style={{boxShadow: '0px 0px 10px #bfbfbf'}}>
          <a href="#home" className="navbar-brand">Chavda Cushion Works</a>
          <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#menu" aria-controls="menu" aria-expanded="true" aria-label="Toggle navigation" style={{ borderColor: "#117c1e" }}>
            <i className="fas fa-bars"></i>
          </button>
          <div className={"navbar-collapse collapse justify-content-end " + this.state.collapsed} id="menu" {...toggle} aria-expanded={this.state.aria}>
            <ul className="navbar-nav">
              <li className="nav-item " >
                <div className="nav-link header-a" >
                <NavLink activeClass=" header-active" to="/">
                  Back to home  
                            </NavLink>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div style={{ textAlign: 'center', marginTop: '10px' }}>
          <div className={classes.grow}>
            <Button variant="extendedFab" className={classNames(classes.margin, classes.cssRoot)} color="primary" onClick={() => this.handeleFilterItem('all')} autoFocus>All</Button>
            <Button variant="extendedFab" className={classNames(classes.margin, classes.cssRoot)} color="primary" onClick={() => this.handeleFilterItem('sofa')}>Sofa</Button>
            <Button variant="extendedFab" className={classNames(classes.margin, classes.cssRoot)} color="primary" onClick={() => this.handeleFilterItem('lounger')}>Lounger</Button>
          </div>
        </div>
        {/* </div> */}
        <ImageDisplay filterImageGroup={filterImageGroup} imageList={imageList} />
      </div>
    );
  }
}

ImageGallery.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(ImageGallery);