import React from 'react';
import { Link } from 'react-scroll';
import { NavLink } from 'react-router-dom';

class NavigationBar extends React.Component {
  constructor() {
    super();
    this.state = {
      collapsed: "collapsed",
      aria: false
    };
  }

  render() {
    let toggle;
    if (window.innerWidth < 992) {
      toggle = {
        "data-toggle": "collapse",
        "data-target": "#menu"
      }
    } else {
      toggle = "";
    }
    return (
      <div className="navbar navbar-expand-lg custome-toggler navbar-light navigation-wrapper master-padding">
        <a href="#home" className="navbar-brand">Chavda Cushion Works</a>
        <button className="navbar-toggler collapsed" type="button" data-toggle="collapse" data-target="#menu" aria-controls="menu" aria-expanded="true" aria-label="Toggle navigation" style={{ borderColor: "#117c1e" }}>
          <i className="fas fa-bars"></i>
        </button>
        <div className={"navbar-collapse collapse justify-content-end " + this.state.collapsed} id="menu" {...toggle} aria-expanded={this.state.aria}>
          <ul className="navbar-nav">
            <li className="nav-item " >
              <div className="nav-link header-a" >
                <Link activeClass=" header-active" to="home" spy={true} smooth={true} duration={200} >
                  Home
                            </Link>
              </div>
            </li>
            <li className="nav-item">
              <div className="nav-link header-a" >
                <Link activeClass=" header-active" to="feature" spy={true} smooth={true} duration={200}>
                  Feature
                            </Link>
              </div>
            </li>
            <li className="nav-item">
              <div className="nav-link header-a" >
                <Link activeClass=" header-active" to="works" spy={true} smooth={true} duration={200}>
                  Gallery
                            </Link>
              </div>
            </li>
            <li className="nav-item">
              <div className="nav-link header-a">
                <Link activeClass=" header-active" to="contact" spy={true} smooth={true} duration={200}>
                  Contact
                            </Link>
              </div>
            </li>
            <li className="nav-item">
              <div className="nav-link header-a">
                <NavLink activeClass=" header-active" to="/gallery">
                  Visit Gallery
                            </NavLink>
              </div>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

export default NavigationBar;